// 
import React, { Component } from 'react';
import { Row, Col } from 'antd';
import '../styles.less'
export default class TeachResource extends Component {
  constructor(props){
    super(props);
    this.state = {
      majorOrdinary:'127',
      majorOne:'425',
      boutiqueOrdinary:'8',
      boutiqueOne:'6'
    }
  }
  render() {
    return (
      <Row type="flex" align="middle" style={{height:"100%",textAlign:"center",color:"#fff",padding:'0 10%'}}>
        <Col span={24} className={'major_visual_teach_resource_title'}></Col>
        <Col span={6} style={{color:'rgba(0,207,255,1)'}}></Col>
        <Col span={6} className={'major_visual_teach_resource_number'}>{this.state.majorOrdinary}</Col>
        <Col span={6} style={{color:'rgba(0,207,255,1)'}}></Col>
        <Col span={6} className={'major_visual_teach_resource_number'}>{this.state.majorOne}</Col>
        <Col span={24} className={'major_visual_teach_resource_title'}></Col>
        <Col span={6} style={{color:'rgba(0,207,255,1)'}}></Col>
        <Col span={6} className={'major_visual_teach_resource_number'}>{this.state.boutiqueOrdinary}</Col>
        <Col span={6} style={{color:'rgba(0,207,255,1)'}}></Col>
        <Col span={6} className={'major_visual_teach_resource_number'}>{this.state.boutiqueOne}</Col>
      </Row>
    )
  }
}
