// 
import React, {Component} from 'react';
import {Col} from 'antd';
import '../styles.less'

export default class CooperativeEnterprise extends Component {
  constructor(props) {
    super(props);
    this.state = {
      EnterpriseNum:'235',
      personNum:'1358',
      classNum:'125',
      bookNum:'104',
      stuNum:'2379',
      EquipmentNum:'1,200,000'
    }
  }
  render(){
    return(
      <Col span={24} style={{
        padding:'10px 20px'
      }}>
        <div>
          <label className={'major_visual_teach_team_label'}></label>
          <span className={'major_visual_teach_team_text'}>{this.state.EnterpriseNum}</span>
        </div>
        <div>
          <label className={'major_visual_teach_team_label'}></label>
          <span className={'major_visual_teach_team_text'}>{this.state.personNum}</span>
        </div>
        <div>
          <label className={'major_visual_teach_team_label'}></label>
          <span className={'major_visual_teach_team_text'}>{this.state.classNum}</span>
        </div>
        <div>
          <label className={'major_visual_teach_team_label'}></label>
          <span className={'major_visual_teach_team_text'}>{this.state.bookNum}</span>
        </div>
        <div>
          <label className={'major_visual_teach_team_label'}></label>
          <span className={'major_visual_teach_team_text'}>{this.state.stuNum}</span>
        </div>
        <div>
          <label className={'major_visual_teach_team_label'}></label>
          <span className={'major_visual_teach_team_text'}>{this.state.EquipmentNum}</span>
        </div>
      </Col>
    )
  }
}
