// 
import React, {Component} from 'react';
import {Col} from 'antd';
import  '../styles.less'

export default class TeachTeam extends Component {
  constructor(props) {
    super(props);
    this.state = {
      num:'232',
      workNum:'3688',
      Equipment:'12673.885',
      AverageInstrument:'12493.7',
      UtilizationRate:'89.72%',
      OutSchoolNum:'120'
    }
  }
  render(){
    return(
      <Col span={24} style={{
        padding:'10px 20px'
      }}>
          <div>
            <label className={'major_visual_teach_team_label'}></label>
            <span className={'major_visual_teach_team_text'}>{this.state.num}</span>
          </div>
          <div>
            <label className={'major_visual_teach_team_label'}></label>
            <span className={'major_visual_teach_team_text'}>{this.state.workNum}</span>
          </div>
          <div>
            <label className={'major_visual_teach_team_label'}></label>
            <span className={'major_visual_teach_team_text'}>{this.state.Equipment}</span>
          </div>
          <div>
            <label className={'major_visual_teach_team_label'}></label>
            <span className={'major_visual_teach_team_text'}>{this.state.AverageInstrument}</span>
          </div>
          <div>
            <label className={'major_visual_teach_team_label'}></label>
            <span className={'major_visual_teach_team_text'}>{this.state.UtilizationRate}</span>
          </div>
        <div>
          <label className={'major_visual_teach_team_label'}></label>
          <span className={'major_visual_teach_team_text'}>{this.state.OutSchoolNum}</span>
        </div>
      </Col>
    )
  }
}
