// 
import React, { Component } from 'react';
import { Row, Col } from 'antd';
import  '../styles.less'
export default class TeachReform extends Component {
  constructor(props){
    super(props);
    this.state = {
      num:'42',
      paper:'38',
      book:'26'
    }
  }
  render() {
    return (
      <Row type="flex" align="middle" style={{height:"100%",textAlign:"center",color:"#fff",padding:'0 10%'}}>
        <Col span={24} className={'major_visual_teach_reform_number_large'}>{this.state.num}</Col>
        <Col span={24} className={'major_visual_teach_reform_title'}>()</Col>
        <Col span={12}>
          <div className={'major_visual_teach_reform_number_normal'}>{this.state.paper}</div>
          <div className={'major_visual_teach_reform_title'} ></div>
        </Col>
        <Col span={12}>
          <div className={'major_visual_teach_reform_number_normal'}>{this.state.book}</div>
          <div className={'major_visual_teach_reform_title'}></div>
        </Col>
      </Row>
    )
  }
}
